.trade-layout {
  display: grid;
  grid-template-columns: 404px 1fr;
  grid-template-rows: 64px 496px auto auto;
  grid-template-areas: "symbol-area trade-info trade-info""account-info kline kline""orderbar kline kline""orderbar my-info my-info""orderbar contract contract";
  gap: 0px;
  border-top: 1px solid #203B60;
  position: relative;


  .orderbar {
    grid-area: orderbar;
    border-right: 1px solid #203B60;
    position: relative;
    .orderbar-box{
      height: 100%;
    }
  }

  .orderbar.retired {
    .retired-trade-mask {
      cursor: not-allowed;
      position: absolute;
      width: 100%;
      height: 100%;
      // z-index: 1;
    }

    .trade-warnning.show .content {
      font-size: 14px !important;
      font-weight: bold;
    }
  }

  .account-info {
    position: relative;
    grid-area: account-info;
    border-right: 1px solid #203B60;
  }

  .symbol-area {
    grid-area: symbol-area;
    border-right: 1px solid #203B60;
    z-index: 10;
  }

  .trade-info {
    grid-area: trade-info;
    height: 64px;
    overflow: hidden;
  }

  .kline {
    grid-area: kline;
    border-top: 1px solid #203B60;
  }

  .my-info {
    grid-area: my-info;
    border-top: 1px solid #203B60;
    z-index: 1;
    overflow: hidden;
  }

  .contract {
    grid-area: contract;
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60
  }

  .trades {
    grid-area: trades;
    border-top: 1px solid #203B60;
  }
}

//小于375px
@media only screen and (min-width: 0px) {
  .trade-layout {
    grid-template-columns: 380px 1fr;
    grid-template-areas: "symbol-area""trade-info""account-info""orderbar""my-info""contract";
    grid-template-rows: auto;
  }

  .trade-layout.mobile-trade-layout {
    grid-template-areas: "symbol-area""kline""orderbar""my-info""contract";

    .kline {
      display: block;
    }

    .account-info,
    .trade-info {
      display: none;
    }
  }

  .symbol-area {
    border-right: 0;
    border-bottom: 1px solid #203B60;
    position: sticky;
    top: 0;
    background: #0A172B;
  }

  .kline {
    display: none;
    border-top: 0;
  }

  .orderbar {
    border-right: 1px solid #203B60;
    // .leverage-bar {
    //   min-height: 586px;
    // }
  }

  .account-info,
  .trade-info {
    display: block;
  }
}

//小于760 大于375px
@media only screen and (min-width : 375px) {
  .trade-layout.mobile-trade-layout {
    grid-template-areas: "symbol-area""kline""orderbar""my-info""contract";

    .kline {
      height: auto;
    }

    .account-info {
      height: 0;
    }
  }

  .trade-layout {
    grid-template-columns: 100%;
    grid-template-areas: "symbol-area""trade-info""account-info""orderbar""my-info""contract";
    grid-template-rows: 64px 1fr;

    .kline {
      border-top: 0px;
      height: 0;
    }

    .symbol-area {
      border-right: 0;
      border-bottom: 1px solid #203B60;
    }

    // .trade-info {
    //   border-top: 1px solid #203B60;
    // }
    .orderbar {
      border-right: 0;
    }

    .contract {
      border-right: 0;
    }

    .account-info {
      height: initial;
      border-right: 0px;
    }

    .my-info {
      overflow: visible;

      .load-more {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 41px;
        background: #0D1A2E;
        border: 1px solid #203B60;
        border-radius: 6px;
        margin-top: 24px;
        color: #E0ECFF;
        font-weight: 600;
        font-size: 14px;
        span {
          cursor: pointer;
        }
      }
    }
  }
}

