//小于1568 大于1140
@media only screen and (min-width : 1140px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px auto 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info""account-info kline kline""orderbar kline kline""orderbar kline kline""orderbar my-info my-info""orderbar contract contract";

    .symbol-area {
      border-right: 1px solid #203B60;
      border-top: 0;
      border-bottom: 0;
    }

    .kline {
      display: block;
      border-top: 1px solid #203B60;
      height: inherit;
    }


    .orderbar {
      border-right: 1px solid #203B60;
    }

    .contract {
      border-top: 1px solid #203B60;
      border-right: 1px solid #203B60
    }

    .trades {
      border-top: 1px solid #203B60;
    }

    .account-info {
      border-right: 1px solid #203B60
    }
  }
}

//小于2056 大于1568
@media only screen and (min-width : 1568px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px minmax(307px, auto) 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info""account-info kline kline""orderbar kline kline""orderbar my-info my-info""orderbar contract contract";

    .symbol-area {
      border-right: 1px solid #203B60;
      border-top: 0;
      border-bottom: 0;
    }

    .kline {
      display: block;
    }

    .orderbar {
      border-right: 1px solid #203B60;
    }

    .contract {
      border-top: 1px solid #203B60;
      border-right: 1px solid #203B60
    }

    .trades {
      border-top: 1px solid #203B60;
    }

    .account-info {
      border-right: 1px solid #203B60
    }
  }

}

@media only screen and (min-width : 2560px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px auto 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info""account-info kline kline""orderbar kline kline""orderbar my-info my-info""orderbar contract contract";

    .symbol-area {
      border-right: 1px solid #203B60;
      border-top: 0;
      border-bottom: 0;
    }

    .kline {
      display: block;
    }

    .orderbar {
      border-right: 1px solid #203B60;

      .leverage-bar {
        min-height: 718px;
      }
    }

    .contract {
      border-top: 1px solid #203B60;
      border-right: 1px solid #203B60
    }

    .trades {
      border-top: 1px solid #203B60;
    }

    .account-info {
      height: initial;
      border-right: 1px solid #203B60
    }
  }
}